.switch{
    display: flex;
    gap:10px;
    padding:20px;
    input{
        display:none;
    }
    label{
        display:flex;
        height: 50px;
        border:1px solid #000;
        padding:0 10px;
        align-items: center;
        justify-content: center; 
        border-radius: 20px;
        width:150px;
    }
    input:checked + label { 
        background-color: #5E496D;
        color:#FFF;
    }
}